a{
    text-decoration:none;
    list-style: none;
    color: #000;
}
body,input,select,textarea,button{
    border: none;
}
ul,ol,li{
    list-style: none;
}

h1{
    color: #ccc;
    position: absolute;
    left: 170px;
    padding-left: 80px;
}
.menu{
    position: absolute;
    padding-left: 30px;
    padding-top: 100px;
}
.menu li{
    margin-bottom: 20px;
    border: solid 1px #ccc;
    text-align: center;
    background: #1c92d2;  
    background: -webkit-linear-gradient(to right, #f2fcfe, #1c92d2);  
    background: linear-gradient(to right, #f2fcfe, #1c92d2); 
    color: black;
    font-size: 22px;
}
.asd{
    float: left;
    padding-left: 250px;
    padding-top: 120px;
    font-size: 18px;
}
.last{
    position: absolute;
    left: 530px;
    top: 100px;
    display: inline-block;
    width: 250px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal;
    text-align: justify;
}
.last h2{
    text-align: center;
    color: rgba(22, 22, 22, 0.212);
}
.active{
   color: #ccc;
   font-size: 30px;
}